import React from 'react';

import { Cards, Chart, CountryPicker } from './components';
import styles from './App.module.css';
import { fetchData } from './api';

import covid from './images/covid.png';

class App extends React.Component {
  state = {
    data: {},
    country: '',
    darkMode: false,
  };

  async componentDidMount() {
    const fetchedData = await fetchData();
    this.setState({ data: fetchedData });
  }

  componentWillMount() {
    const pref = this.getInitialMode();
    this.setState({ darkMode: pref });
    localStorage.setItem('dark', JSON.stringify(pref));
  }

  getInitialMode() {
    const isReturningUser = 'dark' in localStorage;
    const savedMode = JSON.parse(localStorage.getItem('dark'));
    const userPrefersDark = this.getPrefColorScheme();
    // if mode was saved -> dark or light
    if (isReturningUser) {
      return savedMode;
    } else if (userPrefersDark) {
      return true;
    } else {
      // if prefcolorscheme -> dark -> dark otherwise light
      return false;
    }
  }

  getPrefColorScheme() {
    if (!window.matchMedia) return;
    return window.matchMedia('(prefers-color-scheme: dark)').matches;
  }

  handleCountryChange = async (country) => {
    const fetchedData = await fetchData(country);
    this.setState({ data: fetchedData, country: country });
  };

  render() {
    const { data, country, darkMode } = this.state;
    const theme = darkMode ? styles.darkMode : styles.lightMode;
    return (
      <div className={theme}>
        <img className={styles.image} src={covid} alt='COVID19' />
        <div className={styles.toggleContainer}>
          <span style={{ color: darkMode ? 'grey' : 'yellow' }}>ðŸŒ£</span>
          <span className={styles.toggle}>
            <input
              checked={darkMode}
              onChange={() => this.setState({ darkMode: !darkMode })}
              type='checkbox'
              className={styles.checkbox}
              id='checkbox'
            />
            <label htmlFor='checkbox' />
          </span>
          <span style={{ color: darkMode ? 'slateblue' : 'gray' }}>â˜½</span>
        </div>
        <Cards data={data}></Cards>
        <CountryPicker
          handleCountryChange={this.handleCountryChange}
        ></CountryPicker>
        <Chart data={data} country={country}></Chart>
      </div>
    );
  }
}

export default App;
